/*=================  
	Basic 
====================*/
body {
  background-color: #fff;
  font-family: "CerebriSans";
  font-size: 16px;
  font-weight: 400;
  color: #585e6d;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}
a {
  color: #6f6f6f;
  outline: 0 none;
  text-decoration: none;
}
a:hover,
a:focus {
  text-decoration: none;
  outline: 0 none;
}
a:active,
a:hover {
  color: #333333;
}
p a {
  color: #333333;
}
img {
  border-style: none;
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}
hr {
  clear: both;
}
section,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
summary {
  display: block;
}
address {
  font-style: italic;
}
table {
  background-color: transparent;
  width: 100%;
}
table thead th {
  color: #333;
}
table td {
  padding: 15px 10px;
}
p,
address,
pre,
hr,
ul,
ol,
dl,
dd,
table {
  margin-bottom: 24px;
}
::selection {
  color: #000;
  background: #dbdbdb;
}
::-moz-selection {
  color: #000;
  background: #dbdbdb;
}
::-moz-selection {
  background: #3396d1;
  color: #fff;
}
::selection {
  background: #3396d1;
  color: #fff;
}
p {
  line-height: 26px;
  margin-bottom: 24px;
}
iframe {
  border: 0;
}
@media only screen and (max-width: 991px) {
  body {
    font-size: 15px;
  }
  p {
    line-height: 24px;
  }
}

/*=================  
	Headings
====================*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #111111;
  font-family: "CerebriSans", sans-serif;
